.item {
    width: 236px;
    height: 267px;
    background-color: white;
    color: #222222;
    border: 1px solid #ECEFF1;
    display: flex;
    flex-direction: column;
    cursor: pointer;
    text-decoration: none;
    padding: 16px;
    border-radius: 8px;
    margin: 5px;
    align-items: center;
    justify-content: center;
    box-shadow: 0px 0px 6px -3px;
}

.item__image {
    width: 100px;
    height: 100px;
    max-width: 100%;
    position: relative;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    transition: all 0.3s ease-in-out 0s;
}

.item__image:hover {
    transform: scale(1.3);
}

.item__price {
    margin: 20px 10px 5px 10px;
    font-size: 20px;
    font-weight: bold;
}

.item__title {
    font-size: 12px;
    text-align: center;
    min-height: 32px;
    min-width: auto;
    overflow: hidden;
}

.item__stars {
    text-align: center;
    color: #FFDC5E;
}

